.btn-increment{
  height: 50px;
  width: 100px;
  margin: 5px;
  background-color: red;
  color: white;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.btn-reset{
  height: 50px;
  width: 100px;
  margin: 5px;
  background-color: orange;
  color: white;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.btn-decrement{
  height: 50px;
  width: 100px;
  margin: 5px;
  background-color: green;
  color: white;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.btn-incrementMore{
  height: 50px;
  width: 100px;
  margin: 5px;
  background-color: purple;
  color: white;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
